.skeleton{
    opacity: .7;
    animation: skeleton-loading 1s linear infinite alternate;
}

.skeleton-text{
    width: 100%;
    height: .5rem;
    margin-bottom: .25rem;
    border-radius: .125rem;
}

.todo-category .skeleton-text:nth-child(1){
    margin-top: .5rem;
    width: 20%;
}

.todo-intesity .skeleton-text:nth-child(1){
    height: 1rem;
    margin-top: 1rem;
    width: 60px;
}

@keyframes skeleton-loading {
    0%{
        background-color: hsl(201, 20%, 80%);
    }
    100%{
        background-color: hsl(200, 20%, 95%);
    }
}

.todo-info{
    width: 100%;
}

.todo-item {
    text-decoration: none;
    display: flex;
    width: 100%;
    background-color: #fff;
    padding: 15px 20px;
    margin-bottom: 10px;
    border-radius: 10px;
}
.todo-text {
    width: 95%;
}
