.filter-main-container {
    display: flex;
    justify-content: flex-end;
}

.todo-filter-container {
    margin-top: 14px;
    position: relative;
    z-index: 2;
    width: 200px;
}

.todo-filter-container label {
    display: block;
    height: unset;
    width: unset;
    position: unset;
    margin: unset;
}

.option {
    display: none;
}

.button {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 0;
    width: 100%;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid rgb(128, 128, 128);
}

.button span {
    font-family: Lato, sans-serif !important;
    font-weight: 700;
    color: #333;
}

.arrow {
    border: solid black;
    border-width: 0 1px 1px 0;
    padding: 3px;
    margin-right: 8px;
    transform: rotate(45deg);
}

.todo-options-container {
    border: 1px solid rgb(128, 128, 128);
    border-radius: .3rem;
    position: absolute;
    width: 100%;
    top: 65px;
    overflow: hidden;
}

.select-item {
    background-color: #fff;
    padding: 1rem;
    transition: background-color .2s ease;
}

.select-item:hover {
    background-color: #dfdfdf;
}

.hidden {
    display: none;
}

.filter-h1 {
    font-size: 14px;
    color: #aeaeae;
}