.timer-main-container {
    position: absolute;
    z-index: 5;
    width: 100%;
    height: 100%;
    background-color: #fff;
    top: 0;
    left: 0;
}

.timer-container {
    height: 100%;
    display: flex;
    flex-direction: column;
    transition: background-color .4s ease-in;
}

.timer-container.break-mode {
    background-color: #171717 !important;
}

.timer-section-2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    flex-grow: 1;
}


.timer-container .close-icon {
    z-index: 50;
    cursor: pointer;
    margin-top: 20px;
    margin-left: 20px;
    font-size: 25px;
    color: #2E2A38;
    transition: color .2s ease-in;
}

.timer-container .close-icon.break-mode {
    color: #fff;
}

.timer-header {
    display: flex;
    align-items: center;
}

.timer-title {
    padding: 20px;
    width: 100%;
    font-size: 24px;
    text-align: center;
    word-break: break-all;
    color: #4E4A4B;
    font-weight: 500;
    transition: color .2s ease-in;
}

.timer-title.break-mode {
    color: #fff;
}

.timer-clock {
    width: 100%;
    text-align: center;
}

.timer-clock .state {
    font-weight: 600;
    font-size: 20px;
    color: #c4c3c3;
}

.timer-clock .clock {
    font-weight: 500;
    font-size: 78px;
    color: #2E2A38;
    transition: color .2s ease-in;
}

.clock.break-mode {
    color: #fff;
}

.plant-container {
    padding: 25px;
    flex: 1;
    max-height: 254px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.space-container {
    width: 100%;
    max-height: 164px;
    flex: 1;
}

#ew4oU1sdcTE1 {
    max-width: 300px;
}

.pomodoro-balls {
    display: flex;
    gap: 10px;
    width: 100%;
    flex-wrap: wrap;
    padding: 20px 60px;
    justify-content: center;
}


.ball {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    width: 26px;
    height: 26px;
    border-radius: 100%;
    background-color: #d7d2d6;
    transition: transform .3s ease-in, background-color .3s ease-in;
}

.ball.break-mode {
    background-color: #3D3C4C;
}

.ball.selection {
    background-color: #2E2A38;
    transform: scale(1.3);
}

.ball.completed {
    background-color: #51b869;
}

.button-container {
    width: 100%;
    display: flex;
    margin-bottom: 30px;
    justify-content: center;
}

.button-container .button-clock {
    background: #51b869;
    font-size: 20px;
    color: white;
    border-radius: 7px;
    box-shadow: 0 7px 0px #389644;
    border: 3px solid #2a382f;
    width: 80%;
    max-width: 530px;
    transition: all .2s;
    position: relative;
    padding: 20px 25px;
    position: relative;
    top: 0;
    cursor: pointer;
    margin: 0 20px;
}

.button-clock.active {
    box-shadow: 0 7px 0px #963838;
    background: #b85151;
    border: 3px solid #382a2a;
}

.button-clock:active {
    top: 3px;
    box-shadow: 0 2px 0px #387796;
    transition: all .2s;
}

.button-clock:disabled {
    border: 3px solid #858585;
    box-shadow: 0 7px 0px #898989;
    background-color: #cbcbcb;
}

.btn-icon {
    font-size: 30px;
}

@keyframes moveUpAnimation {
    0% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-10px);
    }

    100% {
        transform: translateY(0);
    }
}

@media screen and (max-width: 375px) {
    .plant-container {
        padding: 0;
    }
}

@media screen and (max-height: 650px) {
    .timer-container {
        height: unset;
    }
    .congrats-main-container{
        height: unset;
    }
}