.todo-item {
    text-decoration: none;
    display: flex;
    width: 100%;
    background-color: #fff;
    padding: 15px;
    margin-bottom: 10px;
    border-radius: 10px;
    border: 1px solid #e3e3e3;
    transition: box-shadow .2s ease-in-out;
}

.todo-item:hover{
    box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}
.todo-item.completed{
    opacity: .5;
}

.completed .todo-text{
    text-decoration: line-through;
}


.todo-close-icon {
    padding: 5px 10px 5px 5px;
    height: 20px;
    color: #3e3e3e;
    cursor: pointer;
}

.check-icon{
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    display: none;
    color: #fff;
}


.label-checkbox{
    display: block;
    position: relative;
    height: 22px;
    width: 22px;
}

.todo-count-info{
    padding: 10px 0;
    font-size: 18px;
    font-weight: 700;
}
.todo-count-info span{
    font-size: 25px;
}

.btn-start{
    -webkit-appearance: none;
    appearance: none;
    cursor: pointer;
    background-color: #bfbfbf;
    border: none;
    border-radius: 5px;
    color: #ffffff;
    width: 22px;
    height: 22px;
    transition: background-color .2s ease-in-out;
}

.btn-start:enabled:hover{
    background-color: #4ac32b;
}


input[type="checkbox"]{
    appearance: none;
    -webkit-appearance: none;
    height: 22px;
    width: 22px;
    background-color: #d5d5d5;
    border-radius: 5px;
    cursor: pointer;
    transition: all .2s ease-in-out;
}

input[type="checkbox"]:hover{
    background-color: #a5a5a5;
}
input[type="checkbox"]:checked{
    background-color: #5bcd3e;
}

input[type="checkbox"]:checked ~ .check-icon{
    display: block;
}

.todo-text {
    font-size: 20px;
    font-weight: 500;
    word-break: break-word;
}

.todo-category {
    font-size: 14px;
    font-weight: 500;
    color: #595959;
}

.todo-intensity {
    margin-top: 12px;
    display: inline-block;
    padding: 1px 7px;
    font-size: 15px;
    border-radius: 5px;
}

.todo-intensity.high{
    color: #F75860;
    background-color: #f7586034;
    border: 1px solid #F75860;
}

.todo-intensity.easy{
    color: #4BA9FF;
    background-color: #4ba8ff34;
    border: 1px solid #4BA9FF;
}

.todo-intensity.medium{
    color: #B08CE5;
    background-color: #B08CE534;
    border: 1px solid #B08CE5;
}

.todo-item>*:nth-child(1) {
    flex-grow: 0;
}


.todo-item>*:nth-child(2) {
    flex-grow: 1;
}

.todo-item>*:nth-child(3) {
    flex-grow: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-end;
}