.newtodo-btn{
    width: 70px;
    height: 70px;
    display: inline-block;
    outline: 0;
    cursor: pointer;
    border: 2px solid #000;
    border-radius: 3px;
    color: #fff;
    background: #000;
    font-size: 20px;
    font-weight: 600;
    line-height: 28px;
    padding: 12px 20px;
    text-align:center;
    transition-duration: .15s;
    transition-property: all;
    transition-timing-function: cubic-bezier(.4,0,.2,1);
    position: fixed;
    right: 30px;
    bottom: 30px;
}

.newtodo-btn:hover{
    color: #000;
    background: rgb(255, 218, 87);
}

.add-icon{
    font-size: 30px;
}