* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Quicksand', sans-serif;
}

body {
    background-color: #fafafa;
    position: relative;
    width: 100%;
    height: 100vh;
    overflow-x: hidden;
}

.App {
    padding: 30px;
}

.confetti{
    position: fixed !important;
    top: 0;
    left: 0;
}


h1 {
    font-weight: 700;
    font-size: 40px;
}

@media screen and (min-width:870px) {
    body {
        display: flex;
        justify-content: center;
    }

    .App {
        width: 700px;
    }

    h1,
    h2 {
        text-align: center;
    }

    .newtodo-btn {
        position:sticky;
        width: 100%;
        margin-top: 10px;
    }
}