.searcher{
    width: 100%;
    padding: 15px 20px;
    border-radius: 25px;
    border: 1px solid #e3e3e3;
    background-color: #f5f5f5;
    margin-top: 20px;
    box-shadow: 0 0 0 rgba(0, 0, 0, 0);
    transition: box-shadow 0.3s ease;
    font-size: 16px;
}

.searcher:focus,
.searcher:hover{
    outline: none;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
}