#modal {
    position: absolute;
    top: 0;
    z-index: 3;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.add-modal {
    background-color: #fdfdfd90;
    backdrop-filter: blur(5px);
    padding: 30px;
    width: 100%;
    height: 100%;
    z-index: 2;
}

.add-modal p {
    margin: 10px 0;
}

.add-modal p:nth-of-type(1) {
    margin-top: 40px;
}

.todo-input-name,
.todo-input-category {
    font-size: 18px;
    padding: 8px 15px;
    width: 100%;
    border: 1px solid #dedede;
    border-radius: 5px;
    margin-bottom: 20px;
}

.todo-input-name:required {
    border: 1px solid #FF305D;
}

.todo-input-name:focus,
.todo-input-category:focus {
    outline: none;
    border: 1px solid #a3a3a3;
}

.todo-input-name::placeholder,
.todo-input-category::placeholder {
    color: #bcbcbc;
}

.range-container {
    display: flex;
    position: relative;
    align-items: center;
    flex-direction: column;
    margin-bottom: 20px;
}

.spans-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.todo-input-range {
    width: 95%;
    -webkit-appearance: none;
    appearance: none;
    width: 100%;
    height: 10px;
    background: #ddd;
    outline: none;
    border-radius: 5px;
    margin-bottom: 3px;
}

.todo-input-range::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 15px;
    height: 15px;
    background: #0c0c0c;
    cursor: pointer;
    border-radius: 50%;
}

/* CSS */
.add-modal-btn {
    appearance: none;
    background-color: #1A1A1A;
    border: 2px solid #1A1A1A;
    border-radius: 15px;
    box-sizing: border-box;
    color: #ffffff;
    cursor: pointer;
    display: inline-block;
    font-family: Roobert, -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    font-size: 16px;
    font-weight: 600;
    line-height: normal;
    margin-top: 50px;
    min-height: 60px;
    outline: none;
    padding: 16px 24px;
    text-align: center;
    text-decoration: none;
    transition: all 300ms cubic-bezier(.23, 1, 0.32, 1);
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    will-change: transform;
    width: 100%;
}

.add-modal-btn--cancel {
    margin-right: 15px;
}

.add-modal-btn--add {
    color: #fff;
}

.add-modal-btn:disabled {
    pointer-events: none;
}

.add-modal-btn:hover {
    color: #3B3B3B;
    background-color: transparent;
    box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
    transform: translateY(-2px);
}

.add-modal-btn--add:hover {
    color: #fff;
    transition-duration: .15s;
    transition-property: all;
    transition-timing-function: cubic-bezier(.4, 0, .2, 1);
    color: #000;
    background: rgb(255, 218, 87);
    box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
    transform: translateY(-2px);
}

.add-modal-btn:active {
    box-shadow: none;
    transform: translateY(0);
}

.btn-container {
    display: flex;
    justify-content: space-around;
}

.error_name {
    font-size: 14px;
    color: #FF305D;
}

.add-todo-count {
    display: flex;
    width: 244px;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.controllers {
    height: 48px;
    width: 81px;
    border: 1px solid #ede6d9;
    color: #000;
    cursor: pointer;
    border-radius: none;
    transition: all .1s ease-out;
    -webkit-transform: scale(1);
    transform: scale(1);
    font-size: 20px;
}

.number-input {
    -webkit-appearance: none;
    -webkit-border-radius: 0px;
    -moz-appearance: none;
    appearance: none;
    width: 100%;
    height: 48px;
    background-color: #fff;
    border: 1px solid #ede6d9;
    border-left: 1px solid rgba(0, 0, 0, 0);
    border-right: 1px solid rgba(0, 0, 0, 0);
    border-radius: none;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    transition: all 0.2s ease-out;
}

.number-input:focus {
    background-color: white;
    border: 1px solid #969696;
    outline: none;
}

.number-input::-webkit-inner-spin-button,
.number-input::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

@media screen and (min-width:870px) {
    body {
        height: 100vh;
    }

    #modal {
        justify-content: center;
        align-items: center;
        height: 100%;
        width: 100%;
        position: absolute;
        z-index: 3;
    }


    .add-modal {
        box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
        background-color: #f6f6f6;
        border-radius: 10px;
        width: 500px;
        height: min-content;

    }
}