.congrats-main-container{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 5;
    width: 100%;
    height: 100%;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 50px 20px;
}

.congrats-container{
    height: 400px;
    width: 100%;
}

.congrats-text h1, .congrats-text h2, .congrats-text p{
    word-wrap: break-word;
    color: #fff;
}

.congrats-text h1{
    word-break: break-all;
}


.congrats-container canvas {
    width: 100% !important;
    height: 100% !important;
}

.medal-container{
    width: 100%;
    height: 150px;
}

.congrats-button{
    bottom: 40px;
}


.congrats-button-container {
    width: 100%;
    display: flex;
    justify-content: center;
    position: absolute;
    bottom: 30px;
}

.congrats-button {
    background: #51b869;
    font-size: 20px;
    color: white;
    border-radius: 7px;
    box-shadow: 0 7px 0px #389644;
    border: 3px solid #2a382f;
    width: 80%;
    max-width: 530px;
    transition: all .2s;
    position: relative;
    padding: 20px 25px;
    top: 0;
    cursor: pointer;
    margin: 0 20px;
}

.congrats-button.active {
    box-shadow: 0 7px 0px #963838;
    background: #b85151;
    border: 3px solid #382a2a;
}

.congrats-button:active {
    top: 3px;
    box-shadow: 0 2px 0px #389659;
    transition: all .2s;
}

.congrats-button:disabled{
    border: 3px solid #858585;
    box-shadow: 0 7px 0px #898989;
    background-color: #cbcbcb;
}

.background-square{
    z-index: -1;
    position: absolute;
    width: 50%;
    height: 50%;
    background-color: #389659;
}
.background-square:nth-child(1){
    background-color: #73D9B8;
    top: 0;
    left: 0;
}
.background-square:nth-child(2){
    background-color: #7DDABF;
    top: 0;
    right: 0;
}
.background-square:nth-child(3){
    background-color: #7DDABF;
    bottom: 0;
    left: 0;
}
.background-square:nth-child(4){
    background-color: #73D9B8;
    bottom: 0;
    right: 0;
}

@media screen and (min-width:600px) {
    .congrats-text h1, .congrats-text h2, .congrats-text p{
        text-align: center;
    }
    .congrats-text h1{
        font-size: 60px;
    }
    .congrats-text h2{
        font-size: 40px;
    }
    .congrats-text p{
        font-size: 20px;
    }
}